@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?69046152');
  src: url('../fonts/fontello.eot?69046152#iefix') format('embedded-opentype'),
       url('../fonts/fontello.woff?69046152') format('woff'),
       url('../fonts/fontello.ttf?69046152') format('truetype'),
       url('../fonts/fontello.svg?69046152#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-mail-squared:before { content: '\e800'; }
.icon-linkedin-squared:before { content: '\e801'; }
.icon-github-squared:before { content: '\e802'; }
